.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css2?family=Arima:wght@400&family=Mochiy+Pop+One&display=swap');

.wrapper {
  margin: 20px 50px;
  width: 400px;
}
.wrapper * {
  font-family: 'Arima', '맑은고딕';
}

.title {
  text-align: center;
  font-family: 'Mochiy Pop One', 'Consolas';
}

.btn {
  border: none;
  background: none;
  width: 60px;
  height: 36px;
  border: 1px solid gray;
  border-radius: 320px;
  margin-left: 10px;
}
.btn:hover {
  cursor: pointer;
  box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px #FFF;
}

.addForm {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.addForm label {
  width: 100%;
}
.addForm input {
  padding: 7px;
  font-size: 16px;
  border: none;
  border-bottom: 1px solid gray;
  width: calc(100% - 20px);
}

.addForm input:focus {
  outline: none;
}

.todoList {
  list-style: none;
  padding: 0;
}
.todoList li:before {
  content: '';
  background-color: #333;
  display: inline-block;
  margin-top: -3px;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  margin-right: 10px;
  border-radius: 50%;
}
.todoList li {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
}
.btn-del {
  border: none;
  background-color: pink;
  color: #FFF;
}
.btn-del:hover {
  box-shadow: inset 2px 2px 5px #BF8F97, inset -5px -5px 10px #ffc0cb;
}

@media screen and (max-width: 720px) {
  .wrapper {
    margin: auto;
    width: calc(100% - 50px);
  }
}